import settings from '../app.settings';
import search from './search/search';
import list from './videoList/videoList';
import controls from './controls/controls';
import VideoNode from './videoNode/videoNode';

export default class appManager {
  constructor() {
    this.searchBox = null;
    this.searchButton = null;
    this.resultList = null;
    this.controls = null;
    this.query = null;
    this.pageToken = null;
    this.pageIds = [];
    this.videoData = null;
    this.initStatus = false;
    this.itemsCount = null;
    this.pageWidth = null;
    this.itemsPerPage = null;
    this.size = null;
    this.resultsWidth = null;
    this.page = null;
    this.translate = null;
  }

  init() {
    search.draw();
    list.draw();
    this.searchBox = document.getElementById('search_field');
    this.searchButton = document.getElementById('search_button');
    this.resultList = document.getElementById('result-list');

    const listener = async () => {
      this.resultList.innerHTML = '';
      this.pageToken = null;
      controls.draw(this.initStatus);
      this.query = this.searchBox.value;
      await this.searchQuery();
      await this.getVideoInfo();
      const nodes = new VideoNode(this.videoData);
      this.resultList.appendChild(nodes.create());
      this.initInterface(settings.searchCount, 1);
      if (!this.initStatus) {
        this.create();
      }
      this.initStatus = true;
    };

    this.searchButton.addEventListener('click', listener);
    this.searchBox.addEventListener('keypress', (e) => {
      if (e.keyCode === 13) {
        listener();
      }
    });
  }

  async searchQuery() {
    let url = settings.searchLink + this.query;
    if (this.pageToken) {
      url += `&pageToken=${this.pageToken}`;
    }
    const response = await fetch(url);
    const data = await response.json();
    const ids = [];
    for (let i = 0; i < settings.searchCount; i += 1) {
      ids.push(data.items[i].id.videoId);
    }
    this.pageToken = data.nextPageToken;
    this.pageIds = ids;
  }

  async getVideoInfo() {
    let url = settings.videoLink;
    for (let i = 0; i < this.pageIds.length; i += 1) {
      url += `${this.pageIds[i]},`;
    }
    const response = await fetch(url);
    const data = await response.json();
    this.videoData = data;
  }

  initInterface(itemsCount, page) {
    const wrapper = document.getElementsByClassName('main__wrapper')[0];
    this.itemsCount = itemsCount;
    this.pageWidth = wrapper.offsetWidth;
    this.itemsPerPage = Math.round(this.pageWidth / 360);
    this.size = Math.ceil(this.itemsCount / this.itemsPerPage);
    this.resultsWidth = this.size * this.pageWidth;
    this.page = page;
    this.setResultsWidth();
    this.moveItems();
    this.setPage();
  }

  moveItems() {
    const translateValue = (this.pageWidth * (this.page - 1)).toString();
    this.translate = +translateValue;
    this.resultList.style.transform = `translateX(-${translateValue}px)`;
  }

  setResultsWidth() {
    this.resultList.style.width = `${this.resultsWidth}px`;
  }

  async getMoreItems() {
    await this.searchQuery();
    await this.getVideoInfo();
    const nodes = new VideoNode(this.videoData);
    this.resultList.appendChild(nodes.create());
    this.initInterface(this.itemsCount + settings.searchCount, this.page);
  }

  onResize() {
    window.addEventListener('resize', () => {
      this.initInterface(this.itemsCount, this.page);
      if (this.page > this.size) {
        this.page = this.size;
      }
      this.moveItems();
    }, false);
  }

  onSwipe() {
    this.onTouchSwipe();
    this.onMouseSwipe();
  }

  onTouchSwipe() {
    let xDown = null;
    let xUp = null;
    this.resultList.addEventListener('touchstart', (e) => {
      xDown = e.touches[0].clientX;
    });
    this.resultList.addEventListener('touchmove', (e) => {
      if (!xDown) {
        return;
      }
      xUp = e.touches[0].clientX;
      this.resultList.style.transition = 'none';
      this.resultList.style.transform = `translateX(-${this.translate + xDown - xUp}px)`;
    });
    this.resultList.addEventListener('touchend', () => {
      this.resultList.style.transition = 'transform 1s';
      if (!xDown || !xUp) {
        return;
      }
      const xDiff = xDown - xUp;
      if (xDiff > 0) {
        this.next();
      } else {
        this.prev();
      }
    });
  }

  onMouseSwipe() {
    let xDown = null;
    let xUp = null;
    let check = false;
    this.resultList.addEventListener('mousedown', (e) => {
      xDown = e.clientX;
      check = true;
    });
    this.resultList.addEventListener('mousemove', (e) => {
      if (!xDown) {
        return;
      }
      xUp = e.clientX;
      if (check) {
        this.resultList.style.transition = 'none';
        this.resultList.style.transform = `translateX(-${this.translate + xDown - xUp}px)`;
      }
    });
    this.resultList.addEventListener('mouseup', () => {
      check = false;
      this.resultList.style.transition = 'transform 1s';
      if (!xDown || !xUp) {
        return;
      }
      const xDiff = xDown - xUp;
      if (xDiff > 0) {
        this.next();
      } else {
        this.prev();
      }
    });
  }

  setPage() {
    const controll = document.getElementsByClassName('pageController');
    controll[1].innerHTML = this.page;
    controll[1].dataset.tool = this.page;
    controll[1].classList.add('tool');
    controll[1].addEventListener('animationend', () => {
      controll[1].classList.remove('tool');
    });
  }

  prev() {
    this.page -= 1;
    if (this.page < 1) {
      this.page = 1;
    }
    this.moveItems();
    this.setPage();
  }

  next() {
    this.page += 1;
    this.setPage();
    if (this.page + 1 > this.size) {
      this.moveItems();
      this.getMoreItems();
    } else {
      this.moveItems();
    }
  }

  controlButtons() {
    const controll = document.getElementsByClassName('pageController');
    controll[0].addEventListener('click', () => this.prev(), false);
    controll[2].addEventListener('click', () => this.next(), false);
  }

  create() {
    this.controlButtons();
    this.onResize();
    this.onSwipe();
  }
}
